import { Button, Card, Zoom } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { useContext } from 'react';
import { AppContext } from '../context/appContext';
import colores from '../styles/colors';
import sizeLetter from '../styles/fontSize';
import TypographyStyled from './typografyStyled';
import { isMobile } from '../styles/isMobile';
import qrLink from '../assets/qr_link.png';

const FinalCard = ({ msjf }) => {
	const { reStart, handleMsjFinalCerebro, setRuta } =
		useContext(AppContext);
	const classes = useStyle({ msjf });
	const isGame3 = msjf.startsWith('Las palabras que usamos');

	return (
		<Zoom in={true}>
			<Card className={classes.resultScr}>
				<TypographyStyled
					style={{
						fontSize: isMobile()
							? sizeLetter.cellNormal
							: sizeLetter.normal,
						fontWeight: 'bold',
						color: colores.mainPurple,
					}}
				>
					{!msjf && handleMsjFinalCerebro().txt1}
				</TypographyStyled>
				<br />
				<TypographyStyled
					style={{
						fontSize: isMobile()
							? sizeLetter.cellNormal
							: sizeLetter.normal,
						color: colores.letterSecond,
					}}
				>
					{!msjf ? handleMsjFinalCerebro().txt2 : msjf}
				</TypographyStyled>
				<br />
				{isGame3 === true && (
					<img
						src={qrLink}
						style={{
							height: '30vh',
							borderRadius: 15,
							filter: 'drop-shadow(1px 1px 3px #000)',
						}}
					/>
				)}
				<br />
				<div
					style={{
						display: 'flex',
						flexDirection: 'row',
					}}
				>
					<Button
						variant='contained'
						className={classes.btn}
						onClick={() => reStart()}
					>
						<TypographyStyled
							style={{
								fontWeight: 'bold',
								fontSize: isMobile()
									? sizeLetter.cellSmall
									: sizeLetter.small,
							}}
						>
							Volver a Jugar!
						</TypographyStyled>
					</Button>
					<Button
						variant='contained'
						className={classes.btn}
						onClick={() => setRuta('home')}
					>
						<TypographyStyled
							style={{
								fontWeight: 'bold',
								fontSize: isMobile()
									? sizeLetter.cellSmall
									: sizeLetter.small,
							}}
						>
							Volver al inicio
						</TypographyStyled>
					</Button>
				</div>
			</Card>
		</Zoom>
	);
};

const useStyle = makeStyles((theme) => ({
	resultScr: {
		minHeight: isMobile() ? 300 : 350,
		marginLeft: ({ msjf }) =>
			!isMobile() && (msjf !== undefined ? '18%' : '8%'),
		marginRight: ({ msjf }) =>
			!isMobile() && (msjf !== undefined ? '18%' : '8%'),
		backgroundColor: colores.backgSoft + '!important',
		borderRadius: '15px!important',
		borderBottom: `8px solid ${colores.mainPurple}`,
		padding: isMobile() ? 30 : 50,

		display: 'flex',
		textAlign: 'center',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'space-between',
	},
	btn: {
		backgroundColor: [colores.mainPurple, '!important'],
		marginRight: [20, '!important'],
		marginLeft: [20, '!important'],
	},
}));

export default FinalCard;
